import { useEffect, useRef, useState } from 'react'
import type { DependencyList } from 'react'
import { useLatest } from './useLatest'
import { useUnmount } from './useLifecycle'

const depsAreSame = (
  oldDeps: DependencyList,
  deps: DependencyList
): boolean => {
  for (let i = 0; i < oldDeps.length; i++) {
    if (!Object.is(oldDeps[i], deps[i])) return false
  }
  return true
}

const useEffectTarget = (
  effect: () => void,
  deps: DependencyList,
  target: any
) => {
  const hasInitRef = useRef(false) // 
  const elementRef = useRef<(Element | null)[]>([]) // 
  const depsRef = useRef<DependencyList>([]) // deps
  const unmountRef = useRef<any>() // effect

  //  
  useEffect(() => {
    const targetElement = 'current' in target ? target.current : window

    // 
    if (!hasInitRef.current) {
      hasInitRef.current = true

      elementRef.current = targetElement
      depsRef.current = deps
      unmountRef.current = effect()
      return
    }
    // :  
    if (
      elementRef.current !== targetElement ||
      !depsAreSame(deps, depsRef.current)
    ) {
      //
      unmountRef.current?.()
      //
      elementRef.current = targetElement
      depsRef.current = deps
      unmountRef.current = effect()
    }
  })

  useUnmount(() => {
    unmountRef.current?.()
    hasInitRef.current = false
  })
}

export const useEventListener = (
  event: string,
  handler: (...e: any) => void,
  target: any = window
) => {
  const handlerRef = useLatest(handler)

  useEffectTarget(
    () => {
      const targetElement = 'current' in target ? target.current : window

      //   addEventListener 
      if (!targetElement?.addEventListener) return

      const useEventListener = (event: Event) => {
        return handlerRef.current(event)
      }
      targetElement.addEventListener(event, useEventListener)
      return () => {
        targetElement.removeEventListener(event, useEventListener)
      }
    },
    [event],
    target
  )
}

interface Options {
  onEnter?: () => void
  onLeave?: () => void
}

//  DOM 
export const useHover = (target: any, options?: Options): boolean => {
  const [flag, setFlag] = useState<boolean>(false)
  const { onEnter, onLeave } = options || {}

  useEventListener(
    'mouseenter',
    () => {
      onEnter?.()
      setFlag(true)
    },
    target
  )

  useEventListener(
    'mouseleave',
    () => {
      onLeave?.()
      setFlag(false)
    },
    target
  )

  return flag
}
