import React, { FC, useState } from 'react'
import { Button, message } from 'antd'
import { useCountDown } from '@/hooks/useCountDown'

const CountDown: FC = () => {
  const [, formattedRes] = useCountDown({ targetDate: '2022-12-31 24:00:00' })
  const { days, hours, minutes, seconds, milliseconds } = formattedRes
  const [dcount, setDcount] = useState<number>()

  const [countdown] = useCountDown({
    targetDate: dcount,
    onEnd: () => {
      message.success('')
    }
  })
  return (
    <>
      <div>
         2022-12-31 24:00:00  {days}  {hours}  {minutes} {' '}
        {seconds}  {milliseconds} 
      </div>
      <div>
        <p style={{ marginTop: 12 }}></p>
        <Button
          color="primary"
          disabled={countdown !== 0}
          onClick={() => setDcount(Date.now() + 5000)}
        >
          {countdown === 0 ? '' : ` ${Math.round(countdown / 1000)}s`}
        </Button>
        <Button style={{ marginLeft: 8 }} onClick={() => setDcount(0)}>
          
        </Button>
      </div>
    </>
  )
}

export default CountDown
