import React, { FC } from 'react'
import MockBtn from '@/components/MockBtn'
import {
  Pow,
  Creation,
  CountDown,
  Reaction,
  EventListener,
  LifeCycle
} from './components'
import './index.less'

const Demo: FC = () => {
  return (
    <div className="demo">
      <div className="flex mb8">
        <div className="wrapper mr8">
          <p>usePow hook</p>
          <Pow />
        </div>
        <div className="wrapper mr8">
          <p>MockBtn useCallback</p>
          <MockBtn />
        </div>
        <div className="wrapper mr8">
          <p>Creation useCreation</p>
          <Creation />
        </div>
        <div className="wrapper">
          <p>CountDown </p>
          <CountDown />
        </div>
      </div>
      <div className="flex">
        <div className="wrapper mr8">
          <p>useReactive </p>
          <Reaction />
        </div>
        <div className="wrapper mr8">
          <p> EventListener</p>
          <EventListener />
        </div>
        <div className="wrapper mr8">
          <p> LifeCycle</p>
          <LifeCycle />
        </div>
      </div>
    </div>
  )
}

export default Demo
